<template>
<section class="cart-shopping">
  <div class="container mx-auto p-1">
    <div class="w-full h-auto">
      <div class="flex flex-row items-center justify-start">
        <p class="text-slate-600 text-xs font-thin p-1">فروشگاه انلاین اتما</p>
        <font-awesome-icon :icon="['fas','angle-left']" class="text-slate-500 font-thin mx-1 text-xs"></font-awesome-icon>
        <p class="text-red-600 text-xs font-thin p-1">سبد خرید</p>
      </div>
      <div v-if="list.length > 0">
      <div class="bg-slate-500 p-1 rounded-tr-md rounded-tl-md w-10/12 mx-auto relative mt-2" style="height: 40px">
        <div class="bg-black w-full h-0.5 absolute top-4 right-0"></div>
        <div class="flex flex-col absolute right-20 top-2.5">
          <font-awesome-icon :icon="['fas','circle']" class="text-black text-xs">
          </font-awesome-icon>
          <p class="block text-xs font-bold text-white">سبد خرید</p>
        </div>
        <div class="flex flex-col absolute right-60 top-2.5">
          <font-awesome-icon :icon="['fas','circle']" class="text-black text-xs">
          </font-awesome-icon>
          <p class="block text-xs font-bold text-white">مشخصات گیرنده</p>
        </div>
        <div class="flex flex-col absolute right-96 top-2.5">
          <font-awesome-icon :icon="['fas','circle']" class="text-black text-xs">
          </font-awesome-icon>
          <p class="block text-xs font-bold text-white">روش پرداخت و ارسال</p>
        </div>
        <div class="flex flex-col absolute left-96 top-2.5">
          <font-awesome-icon :icon="['fas','circle']" class="text-black text-xs">
          </font-awesome-icon>
          <p class="block text-xs font-bold text-white">بازبینی سفارش</p>
        </div>
        <div class="flex flex-col absolute left-60 top-2.5">
          <font-awesome-icon :icon="['fas','circle']" class="text-black text-xs">
          </font-awesome-icon>
          <p class="block text-xs font-bold text-white">اتمام خرید</p>
        </div>
      </div>
      <div v-for="(value,index) in list" :key="value.id" class="w-full h-auto mt-3 p-2 border border-1 border-slate-300">
        <table class=" w-full h-auto">
          <thead>
          <tr >
            <th class="w-1/12 bg-slate-300 text-sm font-thin text-slate-600">
              ردیف
            </th>
            <th class="w-4/12 bg-slate-300 text-sm font-thin text-slate-600">
              محصول
            </th>
            <th class=" w-1/12 bg-slate-300 text-sm font-thin text-slate-600">
              حذف
            </th>
            <th class=" w-2/12 bg-slate-300 text-sm font-thin text-slate-600">
              تعداد
            </th>
            <th class="w-2/12 bg-slate-300 text-sm font-thin text-slate-600">
              قیمت واحد
            </th>
            <th class=" w-2/12 bg-slate-300 text-sm font-thin text-slate-600">
              مبلغ کل
            </th>
          </tr>
          </thead>
        </table>
        <div class="flex flex-row items-center justify-between w-full h-auto">
          <p class="w-1/12">{{index+=1}}</p>
          <div class="flex flex-row mx-1 w-4/12">
            <img :src="`${value.img}`" alt="" class="w-14 h-14 rounded-sm">
            <div class="flex flex-col space-y-2">
              <p class="text-slate-600 font-bold text-sm block">{{value.title}}</p>
              <div class="flex flex-row">
                <span class="text-slate-600 font-thin text-xs">{{value.company}}</span>-
                <span class="text-slate-600 font-thin text-xs">{{value.size}}</span>-
                <span class="text-slate-600 font-thin text-xs">{{value.quaranty}}</span>
              </div>
            </div>
          </div>
          <div class="w-1/12 flex flex-row items-center">
            <font-awesome-icon @click="deleteProduct(value.id)" :icon="['fas','trash']" class=" cursor-pointer text-red-500 text-md p-2 font-xs items-center mx-auto"></font-awesome-icon>
          </div>
          <div class="w-2/12 flex flex-row items-center justify-center mx-auto">
            <button type="button" class="bg-green-500 rounded-md mx-1 w-6 h-6 text-white text-xs">
              <span @click="addProduct(value.id)" class="text-white font-bold text-lg p-1">+</span>
            </button>
            <button type="button" class="bg-red-500 text-xs mx-1 w-6 h-6 rounded-md text-white">
              <span @click="decrementProduct(value.id)" class="text-white font-bold text-lg p-1">-</span>
            </button>
            <p class="text-md font-bold text-slate-500">{{value.quantity}}</p>
          </div>
          <div class="w-2/12 flex flex-row items-center">
            <p class="text-green-500 text-lg font-bold items-center" style="text-align: center">{{value.price}}</p>
            <span class="font-bold text-xs text-black p-1">تومان</span>
          </div>
          <div class="w-2/12 flex flex-row items-center">
            <p class="text-black text-lg font-bold">{{calcProduct}}</p>
            <span class="font-bold text-xs text-black p-1">تومان</span>
          </div>
        </div>
        <div class="flex flex-row justify-end w-full">
          <div class="w-3/12 flex flex-row justify-end items-center">
            <p class="text-slate-600 text-sm font-bold ml-4">جمع کل</p>
            <span class="text-green-600 text-md font-thin items-center">{{calcProduct}}</span>
          </div>
        </div>
      </div>
      <div class="flex flex-row items-center w-full">

        <div class="bg-red-600 p-1 w-2/12 rounded-sm mx-auto mt-4">
          <div class="flex flex-row justify-around ">
            <router-link :to="{name:'nextLevel',params:{slug:'user'}}">
            <font-awesome-icon :icon="['fas','angle-left']" class="font-bold text-white"></font-awesome-icon>
            <button type="button" class="font-bold text-white text-md">مرحله بعد</button>
            </router-link>
          </div>
        </div>
      </div>
      </div>
      <div v-else class="relative w-full h-40 border border-1 border-slate-200 rounded-sm">
        <p class="absolute flex flex-row mx-auto text-slate-300" style="right:45%;top:45%">سبد خرید شما خالی است</p>
      </div>
    </div>
  </div>
</section>
</template>

<script>
import {FontAwesomeIcon} from "@fortawesome/vue-fontawesome";
import {useStore} from "vuex";
import{computed} from "vue";
import{useToast} from 'vue-toastification';
import {useRouter} from 'vue-router'
export default {
  name: "CartShopping",
  components: {FontAwesomeIcon},
  setup(){
    const store=useStore()
    const toast=useToast()
    const router=useRouter()
    const list=computed(()=>{
      return store.state.CartProduct.menu
    })
    const calcProduct=computed(()=>{
      return store.getters['CartProduct/calcProduct']
    })
    function addProduct(data){
      store.dispatch('CartProduct/addMyProduct',data)
    }
    function decrementProduct(data){
      store.dispatch('CartProduct/minesMyProduct',data)
    }
    function deleteProduct(data){
      toast.error('محصول از سبد خرید حذف شد!',{
        position:'top-right',
        timeout:5000,
        closeOnClick:true
      })
      store.dispatch('CartProduct/deleteAllProduct',data)
    }
    function transforDate(){
      router.push({alias:'/level'})
    }
    return{
     store,list,calcProduct,addProduct,decrementProduct,deleteProduct,toast,transforDate,
    }
  }
}
</script>

<style scoped>

</style>